<template>
  <div class="secure">
    <van-nav-bar
      title="安全中心"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="btns">
      <van-button type="default" @click="showActionSheet" block
        >修改密码</van-button
      >
      <van-button class="destroy" type="default" block @click="destroyAccount"
        >注销账号</van-button
      >

      <van-button class="destroy" type="info" size="large" @click="logout"
        >退出登陆</van-button
      >
    </div>
    <van-action-sheet v-model="isShow" title="修改密码">
      <van-form @submit="updatePassword">
        <van-field
          v-model="userInfo.oldPassword"
          type="text"
          name="旧密码"
          label="旧密码"
          placeholder="旧密码"
        />
        <van-field
          v-model="userInfo.password"
          type="text"
          name="新密码"
          label="新密码"
          placeholder="新密码"
        />
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
            >提交</van-button
          >
        </div>
      </van-form>
    </van-action-sheet>
  </div>
</template>

<script>
//表单验证模块
import { valid } from "../utils/valid";
//公共方法模块
import { utils } from "../utils/utils";
export default {
  name: "Secure",
  data() {
    return {
      isShow: false,
      userInfo: {
        oldPassword: "",
        password: "",
      },
    };
  },
  created() {
    if (!this.isLogin) {
      this.$router.push({ name: "Login" });
      return;
    }
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  methods: {
    back() {
      this.$router.go(-1);
    },

    showActionSheet() {
      this.isShow = true;
    },

    //修改密码
    updatePassword() {
      if (valid.validForm(this.userInfo, this)) {
        //如果通过

        //判断新旧密码是否一致
        if (this.userInfo.password == this.userInfo.oldPassword) {
          this.$toast("新密码不能和旧密码一样");
          return;
        }

        let tokenString = localStorage.getItem("_t");
        //加载提示
        this.$toast.loading({
          forbidClick: true,
          duration: 0,
          message: "正在修改并退出...",
        });
        this.axios({
          method: "POST",
          url: "/updatePassword",
          data: {
            appkey: this.appkey,
            tokenString,
            oldPassword: this.userInfo.oldPassword,
            password: this.userInfo.password,
          },
          transformRequest: utils.transformRequest,
        })
          .then((d) => {
            this.$toast.clear();

            if (d.data.code === "E001") {
              //修改成功
              this.isShow = false;
              //清除本地存储token
              this.$store.state.isLogin = false;
              localStorage.removeItem("_t");
              this.$store.commit("changeData", {
                key: "isLogin",
                value: false,
              });
              this.$router.push({ name: "Login" });
            } else {
              this.$toast(d.data.msg);
            }
          })
          .catch((err) => {
            this.$toast.clear();
          });
      }
    },

    //退出登陆
    logout() {
      let tokenString = localStorage.getItem("_t");
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在退出登陆...",
      });
      this.axios({
        method: "POST",
        url: "/logout",
        data: {
          appkey: this.appkey,
          tokenString,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();
          this.$store.commit("changeData", { key: "isLogin", value: false });

          localStorage.removeItem("_t");
          this.$router.push({ name: "Login" });
        })
        .catch((err) => {});
    },

    //注销账号
    destroyAccount() {
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "再次确认是否需要注销账号，一旦注销则不可恢复",
        })
        .then((text) => {
          if (text == "confirm") {
            //注销

            let tokenString = localStorage.getItem("_t");
            //加载提示
            this.$toast.loading({
              forbidClick: true,
              duration: 0,
              message: "正在注销...",
            });
            this.axios({
              method: "POST",
              url: "/destroyAccount",
              data: {
                appkey: this.appkey,
                tokenString,
              },
              transformRequest: utils.transformRequest,
            })
              .then((d) => {
                this.$toast.clear();
                this.$store.commit("changeData", {
                  key: "isLogin",
                  value: false,
                });

                localStorage.removeItem("_t");
                this.$router.push({ name: "Login" });
              })
              .catch((err) => {});
          }
        })
        .catch((err) => {});
    },
  },
};
</script>

<style lang="less" scoped>
.secure {
  padding-top: 46px;
  .btns {
    margin-top: 30px;
    padding: 0 10px;
  }
  .destroy {
    margin-top: 30px;
  }
}
</style>