class Valid {
    constructor() {
        //手机号
        this.phone = {
            reg: /^1[3456789]\d{9}$/,
            msg: '请填写正确的手机号'
        }
        //用户名  含有字母  数字字母汉字8位
        //?=.*  验证当前字符串有没有字母，不影响个数
        this.nickName = {
            reg: /^[0-9a-zA-Z\u4e00-\u9fa5]{1,8}$/,
            msg: '用户名需含有1-8位数字，字母，汉字的组合'
        }
        //密码  6-16位除了汉字的字符，首个字符必须是字母
        this.password = {
            reg: /^[a-zA-Z][^\u4e00-\u9fa5]{5,15}$/,
            msg: '密码为6-16位字符，首个字符必须是字母且不能含有汉字'
        }
        //密码  6-16位除了汉字的字符，首个字符必须是字母
        this.oldPassword = {
            reg: /^[a-zA-Z][^\u4e00-\u9fa5]{5,15}$/,
            msg: '旧密码为6-16位字符，首个字符必须是字母且不能含有汉字'
        }
        //邮箱正则表达式
        this.email = {
            reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
            msg: '邮箱格式不正确'
        }
    }
    validForm(o, t) {
        for (let key in o) {
            if (!this[key].reg.test(o[key])) {
                t.$toast(this[key].msg)
                return false
                //表单验证不通过
            }
        }
        return true
    }
}


export const valid = new Valid()